import './App.css';
import CustomNavbar from './component/Navbar';
import {React ,useState,useEffect} from 'react';
import {  Route, Switch,Redirect } from 'react-router-dom'
import Cart from './pages/Cart.js';
import { ToastContainer } from "react-toastify";
import Home from './pages/Home.js';
import Category from './component/Category.js';
import About from './component/About.js';
import Footer from './component/Footer.js';
import MenCategory from './pages/MenCategory.js';
import LoginPage from './pages/Login.js';
import WomenCategory from './pages/WomenCategory.js';
import JeweleryCategory from './pages/JeweleryCategory.js';
import SportsCategory from './pages/SportsCategory.js';
import { transitions, positions, Provider as AlertProvider } from 'react-alert'
import AlertTemplate from 'react-alert-template-basic'
import axios from 'axios';
import ElectornicCategory from './pages/ElectronicCategory.js';

function App({formValues}) {
  const [cart, setCart] = useState([]);
  const incrementCartCount = (product) => {
    setCart([...cart, product])
  }

  const options = {
    position: positions.BOTTOM_CENTER,
    timeout: 3000,
    offset: '30px',
    transition: transitions.SCALE,
  }

  const [isLoggedIn, setLoggedIn] = useState(false);
  const [name, setName] = useState('');
 
  axios.defaults.withCredentials = true;
  useEffect(() =>{
  axios.get('http://localhost:3001/home')
  .then(res =>{
  if(res.status == 200){
    setLoggedIn(true);
    setName(res.data.name);
    }
  else{
  setLoggedIn(false);
      }
  }) 
  .catch(err => console.log(err))
  },[])
    
  function handleLogin() {
    setLoggedIn(true);
  }

  const handleLogout =  () => {
    axios.get('http://localhost:3001/logout')
      setLoggedIn(false);
      setCart(null);
    }
  
  return (
  <>
      <AlertProvider template={AlertTemplate} {...options} >
          <div className="App">
          <header className="App-header">
          <CustomNavbar formValues={formValues} cart={cart} isLoggedIn={isLoggedIn} name={name} onLogout={handleLogout}/>
          </header>
          <ToastContainer />
          <Switch>
          <Route path="/home"  component={() => <Home incrementCartCount={incrementCartCount}/>} />
          <Route path="/cart"  component={() => <Cart cart={cart}/>} />
          <Route path="/categorys"  component={() => <Category />} />
          <Route path="/about"  component={() => <About />} />
          <Route path="/footer"  component={() => <Footer />} />
          <Route path="/category/men-clotihng"  component={() => <MenCategory incrementCartCount={incrementCartCount}/>} />
          <Route path="/category/women-clotihng"  component={() => <WomenCategory incrementCartCount={incrementCartCount}/>} />
          <Route path="/category/electronic"  component={() => <ElectornicCategory incrementCartCount={incrementCartCount}/>} />
          <Route path="/category/jewelery"  component={() => <JeweleryCategory incrementCartCount={incrementCartCount}/>} />
          <Route path="/category/sports"  component={() => <SportsCategory incrementCartCount={incrementCartCount}/>} />
          <Route path="/Login"  component={() => <LoginPage onLogout={handleLogout} onLogin={handleLogin}/>} />
          <Route path="/"  component={() => <Redirect to="/home" />} />
          <Route path=":3001/"  component={() => <Redirect to="/home" />} />
          </Switch>
          </div>
       </AlertProvider>
 </>
  );
}

export default App;
